import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'anexo-dados',   templateUrl: 'anexo_dados.component.html' }) export class AnexoDadosComponent implements OnInit {       displayMode: AnexoDadosDisplayModeEnum;     displayModeEnum = AnexoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = AnexoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = AnexoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = AnexoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum AnexoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


